
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import ResponseData from "@/types/ResponseData";
import Category from "@/types/Category";

export default defineComponent({
  name: "category",
  data() {
    return {
      currentCategory: {} as Category,
      message: "",
      category: [
        {
          _id: "",
          value: "",
          text: "",
          image: "",
        },
      ] as Category[],
      image: {} as Blob,
    };
  },
  methods: {
    getCategory(id: string) {
      DataService.getCategory(id)
        .then((response: ResponseData) => {
          this.currentCategory = response.data;
          console.log(response.data);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    updateCategory() {
      if (!this.currentCategory.value || !this.currentCategory.text) {
        this.message = "Please input Name and Category.";
        return;
      }
      let data = {
        _id: this.currentCategory._id,
        value: this.currentCategory.value,
        text: this.currentCategory.text,
      };
      DataService.updateCategory(data)
        .then((response: ResponseData) => {
          console.log(response.data);
          DataService.uploadImageCategory(
            this.currentCategory._id as string,
            this.image
          )
            .then((response: ResponseData) => {
              console.log(response.data);
              this.message = "The category was updated successfully!";
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    deleteCategory() {
      if (confirm("Are you sure you want to delete this item?")) {
        DataService.delete("category", this.currentCategory._id as string)
          .then((response: ResponseData) => {
            console.log(response.data);
            this.$router.push({ name: "category" });
          })
          .catch((e: Error) => {
            console.log(e);
          });
      }
    },

    onFileChangeImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.currentCategory.image = reader.result as string;
          input.files ? (this.image = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
  },
  mounted() {
    this.message = "";
    this.getCategory(this.$route.params.id as string);
  },
});
