import http from "@/http-common";
import { AxiosResponse } from "axios";
import Product from "@/types/Product";
import Category from "@/types/Category";
console.log(http.defaults);

class DataService {
  getAll(category: string | null): Promise<AxiosResponse<Product[]>> {
    if (category != "" && category != null) {
      return http.get("/product?category=" + category);
    }
    return http.get("/product");
  }

  get(id: string): Promise<AxiosResponse<Product>> {
    return http.get(`/product/${id}`);
  }

  getImages(id: string): Promise<AxiosResponse<Product["images"]>> {
    return http.get(`/product/image/${id}`);
  }

  getEachImage(id: string, index: number): Promise<AxiosResponse<string>> {
    return http.get(`/product/image/${id}/${index}`);
  }

  create(data: Product): Promise<AxiosResponse<string>> {
    return http.post("/product", data);
  }

  uploadImages(id: string, data: Blob[]): Promise<AxiosResponse<string>> {
    const formData = new FormData();

    for (let index = 0; index < data.length; index++) {
      formData.append(index + "", data[index]);
    }

    return http.post(`/product/image/${id}`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  }

  uploadArImage(
    id: string,
    data: Blob[],
    radius: number | null
  ): Promise<AxiosResponse<Product["images"]>> {
    const formData = new FormData();

    formData.append("0", data[0]);

    if (radius != null) {
      return http.post(`/product/arimage/${id}/${radius}`, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
    }
    return http.post(`/product/arimage/${id}`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  }

  update(data: Product): Promise<AxiosResponse<string>> {
    return http.put(`/product`, data);
  }

  delete(collection: string, id: string): Promise<AxiosResponse<string>> {
    return http.delete(`/${collection}/${id}`);
  }

  getAllCategory(): Promise<AxiosResponse<Category[]>> {
    return http.get("/category");
  }

  getCategory(id: string): Promise<AxiosResponse<Category>> {
    return http.get(`/category/${id}`);
  }

  createCategory(data: Category): Promise<AxiosResponse<string>> {
    return http.post("/category", data);
  }

  uploadImageCategory(id: string, data: Blob): Promise<AxiosResponse<string>> {
    const formData = new FormData();

    formData.append("0", data);

    return http.post(`/category/image/${id}`, formData, {
      headers: {
        "Content-Type": "multipart/form-data",
      },
    });
  }

  updateCategory(data: Category): Promise<AxiosResponse<string>> {
    return http.put(`/category`, data);
  }
}

export default new DataService();
