<template>
  <div v-if="currentCategory._id" class="edit-form">
    <h4>Edit Category</h4>
    <form>
      <div class="form-group">
        <label for="id">ID</label>
        <input
          type="text"
          class="form-control"
          id="id"
          v-model="currentCategory._id"
          readonly
        />
      </div>
      <div class="form-group">
        <label for="value">Value<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="value"
          v-model="currentCategory.value"
          required
        />
      </div>
      <div class="form-group">
        <label for="text">Text<span style="color: red"> *</span></label>
        <input
          type="text"
          class="form-control"
          id="text"
          v-model="currentCategory.text"
          required
        />
      </div>
      <div class="form-group">
        <label for="image">Image</label>
        <div
          :class="[
            currentCategory.image
              ? 'custom-file has-image-preview'
              : 'custom-file',
          ]"
        >
          <input
            type="file"
            accept="image/*"
            class="custom-file-input"
            id="image"
            aria-describedby="inputGroupFileAddon01"
            name="image"
            @change="onFileChangeImage"
          />
          <label
            class="custom-file-label has-image-preview"
            for="image"
            v-if="currentCategory.image"
          >
            <img class="preview" :src="currentCategory.image" />
          </label>
          <label class="custom-file-label" for="image" v-else
            >Choose file</label
          >
        </div>
      </div>
    </form>

    <button class="badge badge-danger mr-2" @click="deleteCategory">
      Delete
    </button>

    <button type="submit" class="badge badge-success" @click="updateCategory">
      Update
    </button>
    <p>{{ message }}</p>
  </div>

  <div v-else>
    <br />
    <p>Please click on a Category...</p>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";
import DataService from "@/services/DataService";
import ResponseData from "@/types/ResponseData";
import Category from "@/types/Category";

export default defineComponent({
  name: "category",
  data() {
    return {
      currentCategory: {} as Category,
      message: "",
      category: [
        {
          _id: "",
          value: "",
          text: "",
          image: "",
        },
      ] as Category[],
      image: {} as Blob,
    };
  },
  methods: {
    getCategory(id: string) {
      DataService.getCategory(id)
        .then((response: ResponseData) => {
          this.currentCategory = response.data;
          console.log(response.data);
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    updateCategory() {
      if (!this.currentCategory.value || !this.currentCategory.text) {
        this.message = "Please input Name and Category.";
        return;
      }
      let data = {
        _id: this.currentCategory._id,
        value: this.currentCategory.value,
        text: this.currentCategory.text,
      };
      DataService.updateCategory(data)
        .then((response: ResponseData) => {
          console.log(response.data);
          DataService.uploadImageCategory(
            this.currentCategory._id as string,
            this.image
          )
            .then((response: ResponseData) => {
              console.log(response.data);
              this.message = "The category was updated successfully!";
            })
            .catch((e: Error) => {
              console.log(e);
            });
        })
        .catch((e: Error) => {
          console.log(e);
        });
    },

    deleteCategory() {
      if (confirm("Are you sure you want to delete this item?")) {
        DataService.delete("category", this.currentCategory._id as string)
          .then((response: ResponseData) => {
            console.log(response.data);
            this.$router.push({ name: "category" });
          })
          .catch((e: Error) => {
            console.log(e);
          });
      }
    },

    onFileChangeImage: function (event: Event) {
      var input = event.target as HTMLInputElement;
      if (input.files) {
        var reader = new FileReader();
        reader.onload = () => {
          this.currentCategory.image = reader.result as string;
          input.files ? (this.image = input.files[0]) : null;
        };
        reader.readAsDataURL(input.files[0]);
      }
    },
  },
  mounted() {
    this.message = "";
    this.getCategory(this.$route.params.id as string);
  },
});
</script>

<style>
.edit-form {
  max-width: 500px;
  margin: auto auto 40px auto;
}
.has-image-preview {
  height: 100px;
}
.preview {
  height: 100%;
  object-fit: contain;
}
.has-image-preview .custom-file-label::after {
  content: "Change";
  height: 100px;
  display: flex;
  align-items: center;
}
.badge {
  margin-top: 12px;
  border: none;
  border-radius: 6px;
  padding: 16px 24px;
  color: black;
}
.badge-danger:hover {
  color: white;
}
.badge-success:hover {
  color: white;
}
</style>
